@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans"), local("OpenSans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.button {
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  padding: 14px 78px;
  border: 2px solid #444444;
  font: inherit;
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  color: #444444;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #FFFFFF;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .button {
    padding: 14px 35px;
  }
}
.button--contacts {
  align-self: center;
}
@media (min-width: 320px) {
  .button--contacts {
    padding: 14px 49px;
    margin-top: 36px;
  }
}
.button--contacts:hover,
.button--contacts:focus {
  color: #FFFFFF;
  background: linear-gradient(0deg, #444444, #444444);
}
.button--contacts:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button--contacts {
    padding: 14px 35px;
    align-self: center;
    margin-top: 50px;
    margin-bottom: 31px;
  }
}
@media (min-width: 1150px) {
  .button--contacts {
    padding: 14px 34px;
    margin: 0;
  }
}
.button--reviews:hover,
.button--reviews:focus {
  color: #FFFFFF;
  background: linear-gradient(0deg, #444444, #444444);
}
.button--reviews:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button--reviews {
    position: absolute;
    top: 80px;
    right: 52px;
  }
}
@media (min-width: 1150px) {
  .button--reviews {
    right: 48px;
    top: 85px;
  }
}
.button--form {
  font-size: 17px;
  line-height: 16px;
  color: #FFFFFF;
  background: #63D1BB;
  border: none;
  padding: 19px 82px;
  margin-top: 2px;
  margin-bottom: 46px;
}
@media (min-width: 768px) {
  .button--form {
    padding: 19px 128px;
  }
}
.button--catalog-form {
  padding: 14px 40px;
}
.button--catalog-form:hover,
.button--catalog-form:focus {
  color: #FFFFFF;
  background: linear-gradient(0deg, #444444, #444444);
}
.button--catalog-form:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button--catalog-form {
    padding: 14px 25px;
  }
}
@media (min-width: 768px) {
  .button--product {
    padding: 14px 98px;
  }
}
.button--product:hover,
.button--product:focus {
  color: #FFFFFF;
  background: linear-gradient(0deg, #444444, #444444);
}
.button--product:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--video {
  padding: 14px 20px;
  margin: 0 auto;
  margin-bottom: 32px;
}
.button--video:hover,
.button--video:focus {
  color: #FFFFFF;
  background: linear-gradient(0deg, #444444, #444444);
}
.button--video:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button--video {
    margin-bottom: 41px;
    margin-top: 38px;
    padding: 14px 215px;
  }
}
@media (min-width: 1150px) {
  .button--video {
    margin-top: 23px;
    margin-right: 54px;
    padding: 14px 39px;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;
  }
}
.wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
  .wrapper {
    padding-left: 50px;
    padding-right: 50px;
  }
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  margin: 0;
  min-height: 100%;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  color: #444444;
  background-color: #FFFFFF;
  -webkit-tap-highlight-color: transparent;
  overflow-x: hidden;
}
.page-header {
  font-weight: 700;
  color: #000000;
  background: #FFFFFF;
}
@media (min-width: 768px) {
  .page-header {
    background: #F9F9F9;
  }
}
.main-navigation {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
}
@media (min-width: 320px) and (max-width: 767px) {
  .main-navigation--closed .main-navigation__toggle::before {
    content: "";
    position: absolute;
    top: 11px;
    right: 9px;
    width: 20px;
    height: 14px;
    background: url("../img/menu-open.svg");
  }
  .main-navigation--closed .main-navigation__site-list {
    display: none;
  }
  .main-navigation--closed .main-navigation__user-list {
    display: none;
  }
  .main-navigation--closed .main-navigation__logo {
    display: flex;
  }
  .main-navigation--open .main-navigation__toggle::before,
  .main-navigation--open .main-navigation__toggle::after {
    content: "";
    position: absolute;
    top: 19px;
    left: 2px;
    width: 27px;
    height: 2px;
    background-color: #000000;
    transition: all 0.3s ease-in-out;
  }
  .main-navigation--open .main-navigation__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-navigation--open .main-navigation__toggle::after {
    transform: rotate(-45deg);
  }
  .main-navigation--open .main-navigation__user-list {
    display: block;
  }
  .main-navigation--open .main-navigation__site-list {
    display: block;
  }
  .main-navigation--no-js .main-navigation__toggle {
    display: none;
  }
  .main-navigation--no-js .main-navigation__site-list,
  .main-navigation--no-js .main-navigation__user-list {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-navigation {
    flex-direction: row;
  }
}
.main-navigation ul {
  margin: 0;
  list-style-type: none;
}
.main-navigation a {
  font-weight: 700;
  color: #000000;
  text-decoration: none;
}
.main-navigation__logo {
  display: flex;
  justify-content: center;
  padding: 21px 0 17px 0;
  border-bottom: 1px solid #ECECEC;
  transition: all 0.35s ease-in-out;
}
.main-navigation__logo:hover:not(.current-logo),
.main-navigation__logo:focus:not(.current-logo) {
  opacity: 0.6;
}
.main-navigation__logo:active:not(.current-logo) {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-navigation__logo {
    background: white;
    justify-content: flex-start;
    padding-left: 50px;
    padding-bottom: 14px;
    flex-grow: 1;
  }
}
@media (min-width: 1150px) {
  .main-navigation__logo {
    padding: 0;
    padding-top: 8px;
    align-items: center;
    text-align: center;
    order: 1;
    width: 15%;
    flex-grow: 0;
  }
}
.main-navigation__toggle {
  position: absolute;
  top: 20px;
  right: 22px;
  z-index: 2;
  width: 35px;
  height: 35px;
  padding: 0;
  background-color: #FFFFFF;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
.main-navigation__text {
  display: none;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
@media (min-width: 768px) {
  .main-navigation__text {
    display: flex;
    align-items: center;
    order: 2;
    flex-basis: 34%;
    justify-content: flex-end;
    margin-left: 6px;
    margin-bottom: 22px;
    padding-right: 50px;
  }
}
@media (min-width: 1150px) {
  .main-navigation__text {
    order: 3;
    flex-basis: 39%;
    justify-content: flex-end;
    margin-bottom: 24px;
    margin-left: 3px;
    margin-top: 21px;
  }
}
.site-navigation {
  position: relative;
}
@media (min-width: 320px) {
  .site-navigation {
    padding: 0;
  }
}
@media (min-width: 768px) {
  .site-navigation {
    display: flex;
    flex-wrap: wrap;
    order: 1;
    flex-direction: row;
    flex-basis: 52%;
    padding-left: 50px;
  }
}
@media (min-width: 1150px) {
  .site-navigation {
    order: 0;
    background: white;
    flex-basis: 39%;
    border-bottom: 1px solid #ececec;
  }
  .site-navigation::before {
    content: "";
    position: absolute;
    top: 0;
    right: 490px;
    width: calc(50vw - 575px);
    height: 100%;
    background-color: #ffffff;
  }
}
.site-navigation__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 17px;
  align-items: center;
  border-bottom: 1px solid #ECECEC;
}
@media (min-width: 768px) {
  .site-navigation__item {
    font-size: 15px;
    line-height: 24px;
    padding-left: 0;
    align-items: center;
    border-bottom: none;
  }
}
@media (min-width: 1150px) {
  .site-navigation__item {
    font-size: 17px;
  }
  .site-navigation__item:nth-child(odd) {
    border-right: 1px solid #ECECEC;
  }
  .site-navigation__item:nth-child(2n+2) {
    padding-left: 35px;
  }
}
.site-navigation__link {
  padding: 28px 14px 26px 0;
  transition: all 0.3s ease-in-out;
}
.site-navigation__link[href]:hover,
.site-navigation__link[href]:focus {
  color: rgba(0, 0, 0, 0.6);
}
.site-navigation__link[href]:active {
  color: rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .site-navigation__link {
    padding: 21px 37px 28px 0;
  }
}
@media (min-width: 1150px) {
  .site-navigation__link {
    padding: 31px 37px 26px 0;
  }
}
.user-navigation {
  position: relative;
}
@media (min-width: 320px) {
