  }
}
@media (min-width: 768px) {
  .user-navigation__text {
    display: none;
  }
}
.user-navigation__search {
  position: absolute;
  top: 31px;
  left: -44px;
  fill-opacity: 20%;
}
@media (min-width: 768px) {
  .user-navigation__search {
    top: -7px;
    left: -47px;
  }
}
@media (min-width: 1150px) {
  .user-navigation__search {
    left: -49px;
  }
}
.user-navigation__basket {
  position: absolute;
  top: 30px;
