}
.user-navigation__link {
  position: relative;
  padding: 28px 29px 26px 0;
  transition: all 0.3s ease-in-out;
}
.user-navigation__link--basket {
  padding: 28px 0 26px 16px;
}
.user-navigation__link--basket:hover,
.user-navigation__link--basket:focus,
.user-navigation__link--basket:hover > .user-navigation__basket,
.user-navigation__link--basket:focus > .user-navigation__basket {
