}
.goods-types__link--toys {
  background: #56C3B2;
  box-shadow: 0 10px #56C3B2;
}
.goods-types__link--toys:hover,
.goods-types__link--toys:focus {
  background: #46C1AE;
  box-shadow: 0 10px #46C1AE;
  transform: translateY(-5px);
}
.goods-types__link--toys:hover::before,
.goods-types__link--toys:focus::before {
  opacity: 0.7;
}
.goods-types__link--toys:active {
  background: #46C1AE;
}
.goods-types__link--toys:active::before {
